<template>
<component :is="tagName" v-bind="attrObj" @change="changeValue">
    <slot />
</component>
</template>

<script>
/**
 * @example ../../../examples/docs/examples/TSwitch.md
 */
export default {
    name: 'TSwitch',
    data() {
        return {
            tagName: ''
        }
    },
    model: {
        prop: 'value',
        event: 'change'
    },
    computed: {
        attrObj() {
            let attrs = {}
     
            attrs['disabled'] = this.disabled
            attrs['active-color'] = this.activeColor
            attrs['inactive-color'] = this.inActiveColor
            attrs['active-value'] = this.activeValue
            attrs['inactive-value'] = this.inactiveValue
            attrs['value'] = this.value
            if(this.apptype === 'H5'){
                attrs['size'] = "24px" 
            }

            return attrs
        }
    },
    props: {
        /**
         * 
         *
         */
        value: {
            type: [Boolean, String, Number, Array]
        },
        /**
         * 
         * @value true,false
         */
        disabled: {
            type: Boolean,
            default: () => false
        },
        /**
         * 
         *
         */
        activeColor: {
            type: String,
            default: () => '#409EFF'
        },
        /**
         * 
         *
         */
        inActiveColor: {
            type: String,
            default: () => '#C0CCDA'
        },
        /**
         * 
         *
         */
        activeValue: {
            type: [Boolean,String,Number],
            default: () => true
        },
        /**
         * 
         *
         */
        inactiveValue: {
            type: [Boolean,String,Number],
            default: () => false
        }
    },
    beforeMount() {
        if (this.apptype === 'PC') {
            this.tagName = 'el-switch'
        } else if (this.apptype === 'H5') {
            this.tagName = 'van-switch'
        }
    },
    methods:{
        changeValue(e){
            this.$emit('change',e)
        }
    }
}
</script>
