<template>
<component :is="tagName" v-bind="attrObj">
    <slot />
    
</component>
</template>

<script>
/**
 * @example ../../../examples/docs/examples/TImage.md
 */
export default {
    name: 'TImage',
    data() {
        return {
            tagName: '',

        }
    },
    computed: {
        attrObj() {
            let attrs = {}
            attrs['src'] = this.src
            attrs['fit'] = this.fit
            attrs['alt'] = this.alt
            if(this.apptype === 'PC'){
                attrs['lazy'] = this.lazy
            }else if(this.apptype === 'H5'){
                // attrs['lazy-load'] = this.lazy
            }
            return attrs
        }
    },
    props: {
        /**
         * 
         * 
         */
        src: {
            type: String,
            default: () => ''
        },
        /**
         * 
         * @values fill,contain,cover,none,scale-down
         */
        fit: {
            type: String,
            default: () => 'fill'
        },
        /**
         * alt
         * 
         */
        alt: {
            type: String,
            default: () => ''
        },
        /**
         * 
         * @values true,false
         */
        lazy: {
            type: Boolean,
            default: () => false
        },
        
    },
    beforeMount() {
        if (this.apptype === 'PC') {
            this.tagName = 'el-image'
        } else if (this.apptype === 'H5') {
            this.tagName = 'van-image'
        }
    }
}
</script>
